<script>
import { Bar } from 'vue-chartjs'

export default {
  extends: Bar,
  props: ['tasksStats'],

  mounted() {
    // Overwriting base render method with actual data.
    this.renderChart({
      labels: this.tasksStats.types,
      datasets: [
        {
          label: 'Average Prices Per Task Type',
          backgroundColor: '#D1C4E9',
          data: this.tasksStats.priceAvgs.concat([0, 110])
        }
      ]
    })
  }
}
</script>