<template>
  <section class="task-app">
    <v-btn color="#E0E0E0" @click="goToAdd">Add New</v-btn>
    <Task-List v-if="tasks" :tasks="tasks" @removed="removeTask"></Task-List>
  </section>
</template>

<script>
import TaskList from "../components/Task-List.vue";

export default {
  name: "task-app",
  data() {
    return {};
  },
  computed: {
    tasks() {
      return this.$store.getters.tasksToShow;
    }
  },
  methods: {
    goToAdd() {
      this.$router.push(`/task/edit`);
    },
    removeTask(taskId) {
      this.$store.dispatch({ type: 'removeTask', taskId });
    }
  },
  created() {
    this.$store.dispatch("loadTasks");
  },
  components: {
    TaskList
  }
};
</script>

