<template>
  <section class="task-list">
      <Task-Preview v-for="task in tasks" :key="task._id" :task="task" @removed="removeTask"></Task-Preview>
  </section>
</template>

<script>
import TaskPreview from './Task-Preview.vue'

export default {
  props: ['tasks'],
  methods: {
    removeTask(taskId) {
      this.$emit('removed', taskId)
    }
  },
  components: {
    TaskPreview
  }
}
</script>
