<template>
  <section class="task-details">
    <h1>{{task.name}}</h1>
    <h3>$ {{task.price}}</h3>
    <p>Created at: {{task.createdAt}}</p>
    <p>Type: {{task.type}}</p>
    <h2 v-if="!task.inStock">Out of stock</h2>
  </section>
</template>

<script>
import { taskService } from "../services/task.service.js";

export default {
  data() {
    return {
      task: {}
    };
  },
  created() {
    const taskId = this.$route.params.id;
    taskService.getById(taskId).then(task => (this.task = task));
  }
};
</script>
