<template>
  <section class="dashboard-page">
    <Prices-Chart v-if="AvgPerTaskType" class="prices-chart" :tasksStats="AvgPerTaskType"></Prices-Chart>
  </section>
</template>

<script>
import PricesChart from '../components/Prices-Chart.vue'

export default {
