<template>
  <section class="task-preview">
    <v-card class="mx-auto" width="344" color="#EDE7F6" outlined>
      <v-list-item three-line>
        <v-list-item-content>
          <v-list-item-title class="headline mb-1">{{task.name}}</v-list-item-title>
          <v-list-item-subtitle class="price">$ {{task.price}}</v-list-item-subtitle>
        </v-list-item-content>

        <v-list-item-avatar tile size="80" color="#FAFAFA">
          <img :src=imgSrc />
        </v-list-item-avatar>
      </v-list-item>

      <v-card-actions>
        <v-btn depressed color="white" @click="goToDetails">Details</v-btn>
        <v-btn depressed color="white" @click="goToEdit">Edit</v-btn>
        <v-btn depressed color="white" @click="removeTask">Delete</v-btn>
      </v-card-actions>
    </v-card>
  </section>
</template>

<script>

export default {
  props: ["task"],
  methods: {
    goToDetails() {
      this.$router.push(`/task/details/${this.task._id}`);
    },
    goToEdit() {
      this.$router.push(`/task/edit/${this.task._id}`);
    },
    removeTask() {
      this.$emit('removed', this.task._id)
    }
  },
  computed: {
    imgSrc() {
      return `https://api.adorable.io/avatars/285/${this.task._id}`
    }
  }

};
</script>
