import Vue from 'vue'
import Vuex from 'vuex'
import { taskService } from '../services/task.service.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    tasks: []
  },
  mutations: {
    setTasks(state, { tasks }) {
      state.tasks = tasks
    },
    updateTask(state, { updatedTask }) {
      const idx = state.tasks.findIndex(task => task._id === updatedTask._id);
      state.tasks.splice(idx, 1, updatedTask);
    },
    addTask(state, { addedTask }) {
      state.tasks.unshift(addedTask);
    },
    removeTask(state, { taskId }) {
      const idx = state.tasks.findIndex(task => task._id === taskId);
      state.tasks.splice(idx, 1);
    }
  },
  getters: {
    tasksToShow(state) {
      return state.tasks
    },
    AvgPerTaskType(state) {
      const types = [];
      const numOfTasks = [];
      const pricesSums = [];
      state.tasks.forEach(task => {
        var typeIdx = types.indexOf(task.type)
        if (typeIdx === -1) {
          typeIdx = types.length
          types.push(task.type)
          numOfTasks[typeIdx] = 0;
          pricesSums[typeIdx] = 0;
        }
        numOfTasks[typeIdx]++
        pricesSums[typeIdx] += task.price
      })
      const priceAvgs = pricesSums.map((sum, idx) => {
        return sum / numOfTasks[idx]
      })
      return {types, priceAvgs};
    }
  },
  actions: {
    loadTasks(context) {
      return taskService.query()
        .then(tasks => context.commit({ type: 'setTasks', tasks }));
    },
    removeTask(context, { taskId }) {
      return taskService.remove(taskId)
        .then(() => context.commit({ type: 'removeTask', taskId }))
    },
    updateTask(context, { task }) {
      return taskService.update(task)
        .then(updatedTask => context.commit({ type: 'updateTask', updatedTask }))
    },
    addTask(context, { task }) {
      return taskService.add(task)
        .then(addedTask => context.commit({ type: 'addTask', addedTask }))
    }
  },
  modules: {
  }
})
