<template>
  <section class="task-edit">
    <form @submit.prevent="updateTask">
      <div>
        Task name:
        <input type="text" v-model="task.name" />
      </div>
      <div>
        Price:
        <input type="number" min="0" max="100" v-model="task.price" />
      </div>
      <div>
        Type:
        <input type="text" list="Types" v-model="task.type" />
        <datalist id="Types">
          <option value="Adult">Adult</option>
          <option value="Funny">Funny</option>
          <option value="Educational">Educational</option>
        </datalist>
      </div>
      <v-btn depressed color="white" type="submit" v-if="isNew">Add Task</v-btn>
      <v-btn depressed color="white" type="submit" v-else>Save Changes</v-btn>
    </form>
  </section>
</template>


<script>
import { taskService } from "../services/task.service.js";

export default {
  data() {
    return {
      task: {},
      isNew: false
    };
  },
  methods: {
    updateTask() {
      if (this.isNew) this.$store.dispatch({ type: "addTask", task: this.task });
      else this.$store.dispatch({ type: "updateTask", task: this.task });
      this.$router.push("/");
    }
  },
  created() {
    const taskId = this.$route.params._id;
    if (!taskId) {
      this.isNew = true;
      return
    }
    taskService.getById(taskId)
      .then(task => (this.task = JSON.parse(JSON.stringify(task))));
  }
};
</script>

<style lang="css">
.task-edit {
  text-align: center;
  color: #302d36;
  width: 500px;
  margin: 30px auto 0;
  padding: 40px 0;
  background-color: #ede7f6;
  border-radius: 7px;
}

.task-edit input {
  padding: 5px;
  border: 1px solid #b9b9b9;
  border-radius: 7px;
  margin: 7px;
}

.task-edit button {
  margin-top: 20px;
}
</style>